  margin: 0;
  padding: 0;
}
header {
  box-shadow: 0 4px 8px -1px rgba(0, 0, 0, 0.25);

  // margin-bottom: 20px;
}
section {
  margin: 5px 0;
}

p {
  width: 100%;
}

// .container {
//   margin: 0 auto;
//   /* display: flex; */
// }
.g_input {
  width: 100%;
  margin: 4px 8px;
}

select {
  margin: 4px 8px;
}

.g_section {
  padding: 0 15px;
  flex-basis: 100%;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  display: flex;
  justify-content: space-between;
}
.g_div,
.g_div_100 {
  display: flex;
  flex-basis: 100%;
}
p {
  font-size: 1.25rem;
}
h1 {
  font-size: 1.5rem;
  padding: 8px 0;
  margin: 0;
  text-align: center;
}
header {
  background-color: rgb(63, 81, 181);
  color: white;
}
body {
  background-color: rgb(252, 252, 252);
}

div > div.mdl-selectfield__box > span.mdl-selectfield__arrow-down__container {
  height: 20px;
  width: 20px;
}
.mdl-selectfield.mdl-js-selectfield {
  width: 100%;
  margin: 0 8px;
  // padding: 0 10px;
}
pre {
  margin: 15px;
  width: 100%;
}
#copy {
  margin: 0;
  // margin-right: 15px;
  // left: calc(100% - 122px);
  // bottom: 0;
  // bottom: calc(100% - 46px);
  width: 120px;
}
#generatezip {
  justify-content: center;
  margin: 15px;
}
#zip {
  height: 60px;
  width: 70%;
  font-size: 1.5rem;
  font-weight: 700;
}

.drop .cont .browse {
  min-height: 36px;
  margin: 0;
  color: white;
  padding: 8px 16px;
  border-radius: 5px;
  background: rgb(255, 64, 129);
  font-size: 1rem;
  text-transform: uppercase;
  font-weight: 700;
  width: 60%;
  margin: 0 auto;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
  width: 90%;
}

// #input > div:nth-child(10) > div > div.mdl-selectfield__box > span.mdl-selectfield__arrow-down__container > span {
//   width: 45px;
// }

.drop {
  display: flex;
  align-content: center;
  width: 100%;
  height: 100%;
  border: 3px dashed #dadfe3;
  border-radius: 15px;
  overflow: hidden;
  text-align: center;
  background: white;
  -webkit-transition: all 0.5s ease-out;
  -moz-transition: all 0.5s ease-out;
  transition: all 0.5s ease-out;
  margin: auto;
  position: relative;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  /*&:hover
   * cursor: pointer
   * background: #f5f5f5 */
}
.dropenter {
  border: 3px solid rgb(63, 81, 181);
  color: rgb(63, 81, 181);
}
.blueish {
  color: rgb(63, 81, 181);
}
.drop .cont {
  width: 90%;
  height: auto;
  color: #8e99a5;
  -webkit-transition: all 0.5s ease-out;
  -moz-transition: all 0.5s ease-out;
  transition: all 0.5s ease-out;
  margin: auto;
}
.drop .cont i {
  font-size: 2rem;
  color: #8e99a5;
  position: relative;
}
.drop .cont .tit {
  font-size: 2rem;
  text-transform: uppercase;
}
.drop .cont .desc {
  color: #a4aebb;
}
.drop .cont .browse {
  min-height: 36px;
  margin: 0;
  color: white;
  padding: 8px 16px;
  border-radius: 5px;
  background: rgb(255, 64, 129);
  font-size: 1rem;
  text-transform: uppercase;
  font-weight: 700;
  width: 60%;
  margin: 0 auto;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
  width: 80%;
}
.drop input {
  width: 100%;
  height: 100%;
  cursor: pointer;
  background: red;
  opacity: 0;
  margin: auto;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}

#list {
  width: 100%;
  text-align: left;
  position: absolute;
  left: 0;
  top: 0;
}
#list .thumb {
  height: 75px;
  border: 1px solid #323a44;
  margin: 10px 5px 0 0;
}
#upload_icon {
  font-size: 4rem;
}
h5 {
  margin: 0;
  font-weight: 700;
}
a {
  text-decoration: none;
  color: rgb(255, 64, 129);
}

pre {
  display: flex;
  flex-wrap: wrap;
  font-family: 'Inconsolata', monospace;
  font-weight: 700;
  font-size: 1.3rem;

  background-color: white;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  border-radius: 3px;
  padding: 10px 20px;
  margin: 20px;
  border-radius: 4px;
  width: 100%;
  padding: 0 5px;
  margin: 15px 0;
}
code {
  margin: 5px 0;
  //   overflow-x: auto;
  //   white-space: pre-wrap;
  //   white-space: -moz-pre-wrap;
  //   white-space: -pre-wrap;
  //   white-space: -o-pre-wrap;
  //   word-wrap: break-word;
}
code#json_code > span {
  word-wrap: break-word !important;
}

.json-key {
  color: rgb(255, 64, 129);
}
.json-string {
  color: rgb(63, 81, 181);
}
#footer {
  text-align: center;
  box-shadow: 0 4px 8px -1px rgba(0, 0, 0, 0.25);

  box-shadow: 0px -4px 5px 0px rgba(0, 0, 0, 0.25);

  background-color: transparent;
  height: 50px;
  padding-top: 10px;
}
footer p {
  padding: 0;
  margin: 0;
}

.wrapper {
  height: 200px;
  width: 100%;
}
@media only screen and (min-width: 600px) {
  h1 {
    font-size: 2.5rem;
    padding: 8px 0;
    margin: 0;
    text-align: center;
  }
  main {
    display: flex;
  }

  .g_section {
    flex-basis: 50%;
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
  }

  .g_div {
    display: flex;
    flex-basis: 100%;
  }
  #zip {
    width: 100%;
  }
}
@media only screen and (min-width: 960px) {
  main {
    margin: 75px 0;
    // min-height: 90vh;
  }
  #zip {
    width: 60%;
  }
  main {
    display: flex;
  }

  .g_section {
    flex-basis: 50%;
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
  }

  .g_div {
    display: flex;
    flex-basis: 50%;
  }
  .drop .cont .browse {
    width: 50%;
  }
}
